@import "/src/style/variables.scss";

.hero-slider {
    &__slide-container {
        @media (max-width: 1500px) {
            padding-left: 110px;
            padding-right: 110px;
        }
    }
    & .swiper {
        height: 800px;
    }
    & .swiper-black-text {
        color: $main-color-grey-900;
    }
    & .swiper-white-text {
        color: #fff;
    }
    & .swiper-button-prev,
    .swiper-button-next {
        width: 48px;
        height: 48px;
        border-radius: 50%;
    }
    & .swiper-button-next {
        background: url("/img/hero-next-btn.svg") center/24px no-repeat;
        background-color: #fff;
        margin-right: 32px;
    }
    & .swiper-button-prev {
        background: url("/img/hero-prev-btn.svg") center/24px no-repeat;
        background-color: #fff;
        margin-left: 32px;
    }

    & .swiper-pagination {
        @media (max-width: 1500px) {
            padding-left: 110px;
            padding-right: 110px;
        }
        bottom: 180px;
        gap: 4px;
        font-family: "Ubuntu";
        font-weight: 700;
        font-size: 28px;
        color: $main-color-grey-800;
    }

    & .swiper-pagination-bullet {
        width: 100%;
        max-width: 178px;
        padding-bottom: 12px;

        & .underline {
            position: absolute;
            left: 0;
            bottom: -2px;
            width: 100%;
            height: 2px;
            transition: opacity 0.3s ease, background-color 0.2s ease;
            background-color: $main-color-grey-800;
            opacity: 0.3;
        }
    }

    & .swiper-pagination-bullet-active {
        opacity: 1;
        & .underline {
            opacity: 1;
        }
    }
    & .swiper-pagination-bullet-black {
        color: $main-color-grey-900;
        & .underline {
            background-color: $main-color-grey-900;
        }
    }
    & .swiper-pagination-bullet-white {
        color: #fff;
        & .underline {
            background-color: #fff;
        }
    }

    &__slide-subheading {
        margin-top: 140px;
        font-weight: 700;
        font-size: 18px;
        line-height: 150%;
    }
    &__slide-heading {
        margin-top: 12px;
    }
    &__buttons {
        margin-top: 60px;
        display: flex;
        gap: 24px;
    }
}
